*{
  
  padding:0;
  margin:0;
  background-color: #d2dbdd;
}
.card{
  margin:6%;
  transition: all 0.4s cubic-bezier(0.175,0.885,0,1);
  width:21.25%;
  border-radius: 12px;
  background-color: #fff;
  box-shadow: 0px 13px 10px -7px rgba(0,0, 0,0.1);
  float:left;
}
.cards{
  width:100%;
  height:auto;
}
.card_img{
  width:100%;
  height:235px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}
.card_info{
  background-color: #fff;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  padding: 16px 24px 24px 24px;
}
.card:hover{
  box-shadow:0px 30px 18px -8px rgba(0,0,0,0.1);
  transform:scale(1.05,1.05);
}
.card_category{
  text-transform: uppercase;
  font-size:13px;
  letter-spacing: 2px;
  color:#868686;
  background-color: #fff;
}
.card_title{
  text-transform: uppercase;
  background-color: #fff;
  margin-top: 5px;
  margin-bottom: 10px;
}
button{
  padding:5px 8px;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color:#868686;
  background-color: #fff;
  outline:none;
  border:1px solid  black;
  cursor: pointer;
}
.heading{
  text-align: center;
  font-weight:500;
  font-size: 2rem;
  text-transform: uppercase;
  background-color:white;
  color:#868686;
  letter-spacing:2px;
  padding:30px 0;
  
}